var swiper = new Swiper(".mySwiper", {
   slidesPerView: 4,
	spaceBetween: 50,
   pagination: {
      el: ".swiper-pagination",
      type: "progressbar",
   },
   navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
   },
   breakpoints: {
      375: {
			
         slidesPerView: 2,
      },
		320: {
			
         slidesPerView: 1,
      }
   }

});

